$card-title: rgb(66, 66, 66);
$card-town: rgb(116, 116, 116);
$card: #f5f5f5;

.card_map{
    margin-bottom: 70px;
    .map_margin{
        *{
            z-index: 1000 !important;
        }
        margin: 15px 0;
        img{
            width: 100%;
            max-height: 300px;
            object-fit: cover;
        }
    }
    .row{
        width: 100% !important;
        display: flex;
        justify-content: flex-start;
        margin: 0 !important;
        .town{
            color: $card-town;
            font-size: 13px;
        }
        .title{
            color: $card-title;
            font-size: 16px;
        }
        .bold{
            font-weight: bold !important;
        }
        button{
            margin-top: 25px;
            background: #837BFD;
            border-radius: 20px;
            height: 28px;
            min-width: 90px;
            max-width: 90px;
            border: none;
            color: white;
        }
    }
    min-height: 460px;
    width: 105%;
    // background: $card;
    // box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
    // border-radius: 25px;
    display: flex;
    flex-direction: column;
    // padding: 20px;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
    .left{
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 50%;
        justify-content: space-between;
        .title{
            font-size: 14px;
            color: $card-title;
            font-weight: 700 !important;
        }
        .town{
            color: $card-town;
            font-size: 12px;
            margin-top: 5px;
        }
        button{
            background: #837BFD;
            border-radius: 19px;
            min-width: 85px;
            max-width: 85px;
            border: none;
            height: 25px;
            color: rgb(255, 255, 255);
            font-weight: 200;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            padding-bottom: 2px;
        }
    }
    img{
        height: 100%;
        width: auto;
    }
    #logist{
        position: relative;
        right: 25px;
    }
}

.search{
    position: fixed;
    top: 7vh;
    border: none !important;
    background: #e4e4e49c !important;
    backdrop-filter: blur(5px);
    max-width: 270px;
    min-width: 64%;
    height: 35px;
    border-radius: 20px;
    padding-left: 15px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    a{
        margin: 0 !important;
        padding: 0 !important;
        height: 18px !important;
    }
    img{
        height: 15px;
        width: 15px;
        margin-right: 10px;
    }
    .search_input{
        border: none !important;
        height: 100%;
        width: 90%;
        background: none !important;
        color: rgb(70, 70, 70);
        &::placeholder {
            color: rgb(126, 126, 126)!important;
            font-weight: 500 !important;
            font-size: 13px !important;
        }
    }
}