$card-title: rgb(66, 66, 66);
$card-town: rgb(116, 116, 116);
$card: #f5f5f5;

.container_vacancy{
    width: 78vw !important;
    margin-top: 140px !important;
    margin-bottom: 140px !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    flex-direction: column;
    min-height: 520px;
    max-height: 540px;
    .loading{
        height: auto;
        width: 65%;
    }
    .search{
        a{
            margin: 0 !important;
            padding: 0 !important;
            height: 18px !important;
        }
        position: fixed;
        top: 7vh;
        border: none !important;
        background: #e4e4e49c !important;
        backdrop-filter: blur(5px);
        max-width: 270px;
        min-width: 64%;
        height: 35px;
        border-radius: 20px;
        padding-left: 15px;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        img{
            height: 15px;
            width: 15px;
            margin-right: 10px;
        }
        .search_input{
            border: none !important;
            height: 100%;
            width: 90%;
            background: none !important;
            color: rgb(70, 70, 70);
            &::placeholder {
                color: rgb(126, 126, 126)!important;
                font-weight: 500 !important;
                font-size: 13px !important;
            }
        }
    }
}