.wrapper{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    .loading{
        height: auto;
        width: calc(50% + 1vh);
    }
    .search{
        a{
            margin: 0 !important;
            padding: 0 !important;
            height: 18px !important;
        }
        position: fixed;
        top: 7vh;
        border: none !important;
        backdrop-filter: blur(5px);
        background: #d9d9d99c !important;
        max-width: 270px;
        min-width: 64%;
        height: 35px;
        border-radius: 20px;
        padding-left: 15px;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        img{
            height: 15px;
            width: 15px;
            margin-right: 10px;
        }
        .search_input{
            border: none !important;
            height: 100%;
            width: 90%;
            background: none !important;
            color: rgb(70, 70, 70);
            &::placeholder {
                color: rgb(126, 126, 126)!important;
                font-weight: 500 !important;
                font-size: 13px !important;
            }
        }
        
    }
}