$color-darkgray: rgb(41, 41, 41);

$green: #56af92;
$dark-green: #95FCDA;

$white: rgb(250, 250, 250);

$purple: #5a56af;


@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');


.logo{
    height: 40px;
}

.back{
    position: fixed;
    height: 100vh;
    width: 100vw;
    z-index: -1;
    top: 0;
    left: 0;
    // background: url('../img/1260.png');
    // background-size: cover;
    // background-position: center;
}
.container{
    width: 45vw;
    background: transparent;
    .transpanent_block{
        background: transparent;
        height: 70px;
        width: 100%;
    }
    .header{
        font-size: 25px;
        font-weight: 700;
        color: $color-darkgray;
        // color: #fff !important;
    }
    .text_container{
        padding: 70px 0;
        color: $color-darkgray;
        // color: #fff !important;
        #margin{
            margin: 100px 0;
        }
        .photo_row{
            display: flex;
            justify-content: space-between;
            width: 100%;
            height: 400px;
            margin: 50px 0;
            align-items: center;
            border-bottom: 1px solid rgb(189, 189, 189);
            .left{
                font-family: 'Montserrat', sans-serif;
                width: 45%;
                .header1{
                    color: $dark-green;
                    font-size: calc(20px + 1vh);
                    font-weight: 800;
                    // border-bottom: 1px solid $dark-green;
                    filter: contrast(30%);
                    width: auto;
                    display: inline-block;
                    margin: 5px 0;
                }
                .header2{
                    color: $dark-green;
                    font-size: calc(17px + 1vh);
                    font-weight: 800;
                    // border-bottom: 1px solid $dark-green;
                    filter: contrast(50%);
                    width: auto;
                    display: inline-block;
                    margin: 5px 0;
                }
                .header3{
                    color: $dark-green;
                    font-size: calc(15px + 1vh);
                    font-weight: 800;
                    // border-bottom: 1px solid $dark-green;
                    filter: contrast(70%);
                    width: auto;
                    display: inline-block;
                    margin: 5px 0;
                }
            }
            .right{
                width: 45%;
                object-fit: contain;
                margin: 0 !important;
            }
        }
        .center{
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 90px 0 120px;
            .send{
                display: flex;
                justify-content: space-between;
                width: 80%;
                .text{
                    display: flex;
                    flex-direction: column;
                    width: 50%;
                    padding-right: 70px;
                    h2{
                        color: $color-darkgray;
                        // color: #fff;
                    }
                    p{
                        margin: 0;
                        padding: 0;
                        font-weight: 400;
                        margin-top: 10px;
                        font-size: calc(6px + 1vh);
                        text-align: left;
                    }
                    
                }
                form{
                    display: flex;
                    flex-direction: column;
                    width: 40%;
                    padding: 15px 0;
                    input{
                        border: 1px solid $color-darkgray;
                        background: transparent;
                        box-sizing: border-box;
                        width: 100%;
                        height: 32px;
                        margin: 6px 0;
                        padding-left: 8px;
                        display: flex;
                        align-items: center;
                        color: $color-darkgray;
                        &::placeholder {
                            color: $color-darkgray;
                            font-weight: 500;
                            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                            font-size: 13px;
                        }
                    }
                    textarea{
                        border: 1px solid $color-darkgray;
                        background: transparent;
                        box-sizing: border-box;
                        min-width: 100%;
                        max-width: 100%;
                        max-height: 340px;
                        min-height: 150px;
                        margin: 6px 0;
                        padding-left: 8px;
                        padding-top: 8px;
                        display: flex;
                        align-items: center;
                        color: $color-darkgray;
                        &::placeholder {
                            color: $color-darkgray;
                            font-weight: 500;
                            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                            font-size: 13px;
                        }
                    }
                    button{
                        background: black;
                        color: white;
                        height: 32px;
                        width: 100px;
                        margin: 7px 0;
                    }
                }
            }
            p{
                text-align: center;
                font-size: calc(11px + 2vh);
                font-weight: 600;
            }
            .line{
                width: 100px;
                height: 10px;
            }
        }
        // .photo{
        //     width: 100%;
        //     height: 450px;
        //     margin: 50px 0;
        //     border-radius: 10px;
        //     object-fit: cover;
        //     transition: ease-in-out 0.5s,
        // }
        .title{
            font-size: calc(6px + 1vh);
            font-weight: 400;
        }
        .row{
            margin-top: 120px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            .left{
                width: 60%;
                video{
                    width: 100%;
                    border-radius: 10px;
                    border: none;
                    margin-bottom: 100px;
                }
            }
            .right{
                width: 35%;
                height: 15vh;
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;
                flex-direction: column;
                #diadoc{
                    width: 55%;
                }
                #diadoc_p {
                    margin-top: 0;
                    padding-left: 10px;
                    margin-bottom: 5px;
                }
                img{
                    width: 50%;
                    margin: 0;
                    height: auto;
                    object-fit: contain;
                }
                p{
                    margin-top: 15px;
                    padding-left: 10px;
                }
            }
        }
    }   
}


@media screen and (min-width: 900px) and (max-width: 1200px) {
    .container{
      width: 65vw;
    }
    .photo{
        height: 300px!important;
    }
}

@media screen and (min-width: 350px) and (max-width: 900px) {
    .container{
        width: 85vw!important;
    }
    .photo{
        height: 300px!important;
    }
    .send{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 85vw !important;
        .text{
            display: flex;
            flex-direction: column;
            width: 100% !important;
            padding-right: 70px;
            h2{
                color: $color-darkgray;
            }
            p{
                margin: 0;
                padding: 0;
                font-weight: 400;
                margin-top: 10px;
                font-size: calc(6px + 1vh);
                text-align: left;
            }
        }
        form{
            width: 100% !important;
        }
    }
    .row{
        flex-direction: column !important;
        margin-top: 55px !important;
        .left{
            width: 100% !important;
            height: 35vh;
        }
        .right{
            flex-direction: column !important;
            width: 100% !important;
            min-height: 190px !important;
            justify-content: space-between !important;
            align-items: center !important;
            p{
                width: 100% !important;
                padding: 0 !important;
            }
        }
    }
    .photo_row{
        background: $white;
        border-bottom: none !important;
        height: 25vh !important;
        border-radius: 10px;
        justify-content: center !important;
        .left{
            display: none;
        }
    }
    #main *{
        color: $color-darkgray !important;
        .btn{
            color: $white !important;
        }
    }
    .title{
        font-weight: 400 !important;
    }
    .header{
        color: $green !important;
        font-size: calc(13px + 1vh) !important;
    }
    .right{
        width: 60% !important;
    }
}