$card-title: rgb(66, 66, 66);
$card-town: rgb(116, 116, 116);
$card: #f5f5f5;

.card{
    height: 160px;
    width: 100%;
    background: $card;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
    border-radius: 25px;
    display: flex;
    flex-direction: row;
    padding: 20px;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
    .left{
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 50%;
        justify-content: space-between;
        .title{
            font-size: 14px;
            color: $card-title;
            font-weight: 700 !important;
        }
        .town{
            color: $card-town;
            font-size: 12px;
            margin-top: 5px;
        }
        button{
            background: #837BFD;
            border-radius: 19px;
            min-width: 85px;
            max-width: 85px;
            border: none;
            height: 25px;
            color: white;
            font-weight: 200;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            padding-bottom: 2px;
        }
    }
    img{
        height: 100%;
        width: auto;
    }
    #logist{
        position: relative;
        right: 20px;
    }
}